body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* FLEX */
.flex {
  display: flex;
}

.vertical { flex-direction: column; }
.horizontal, .horizontal-only { flex-direction: row; }
@media screen and (max-width: 720px){
  .horizontal { flex-direction: column; }
}

.flex-1 { flex: 1; }
.flex-2 { flex: 2; }
.flex-3 { flex: 3; }
.flex-4 { flex: 4; }
.flex-5 { flex: 5; }
.flex-6 { flex: 6; }
.flex-7 { flex: 7; }
.flex-8 { flex: 8; }
.flex-9 { flex: 9; }
.flex-10 { flex: 10; }

.flex-center {
  justify-content: center;
  align-items: center;
}

/* CONTAINER */
.container, .container-big {
  width: calc(100% - 20px);
  max-width: 1024px;
  margin: 0 auto;
  padding: 10px;
}
.container-big {
  max-width: 1250px;
}

/* INPUT */
.form-input {
  padding: 10px 15px;
  margin: 3px;
  border: 0;
  border-radius: 10px;
  font-size: 1rem;
  width: calc(90%)
}

/* TEXT */

.text-center {
  text-align: center;
}

.titulo {
  margin: 15px 0 0 0;
}

/* COMPONENTS */

.TopBar {
  background-color: #222;
  padding: 15px 0;
}

.NavBar {
  background-color: #F1B522;
  padding: 8px 0;
}

.categorias {
  background-color: #222;
  border-radius: 10px;
  margin: 2px;
}
.categorias > .form-input {
  background-color: transparent;
  color: #fff;
}

.pic-container {
  height: 120px;
}
.pic-main-container {

}
.pic-main {
  height: 350px;
}
.pic-sub-container {
  
}
.pic-sub {
  height: 100%;
  width: 100px;
  border: 1px #777 solid;
  cursor: pointer;
}

.price {
  font-size: 2.5rem;
  margin: 0;
}
.price > small {
  font-size: 1rem;
  color: #555;
}
.price-min {
  font-size: 1.5rem;
  margin: 0;
}
.sub-price {
  margin: 5px;
  font-size: 0.9rem;
  color: #474747;
}

.btn-recomendacoes, .btn-cta {
  padding: 12px 30px;
  background-color: rgb(227, 6, 6);
  font-size: 1rem;
  border: 0;
  border-radius: 10px;
  color: #fefefe;
  cursor: pointer;
}
.btn-recomendacoes:hover, .btn-cta:hover {
  background-color: rgb(145, 6, 6);
}
.btn-cta {
  font-size: 1.3rem;
}

.pic-recomendacao {
  width: 80%;
}

.Avisos {
  background-color: #F1B522;
  padding: 15px 0;
  margin: 10px 0;
}
.margin-5 {
  margin: 5px;
}

.Contato {
  background-color: #222;
  color: #fefefe;
}

.Institucional {
  background-color: #F1B522;
  padding-bottom: 15px;
}

.Footer {
  background-color: #222;
  color: #fefefe;
  padding: 5px 0;
}

/* MOBILE */
@media screen and (max-width: 720px){
  .mb-padding-10 {
    padding: 10px 0;
  }
}